import React, { useContext, useState, useEffect } from 'react';
import {
    Link, NavLink
} from "react-router-dom";

import { ThemeContext } from "../../contexts/theme_context"

import {
    Nav,
    NavDropdown
} from "react-bootstrap"

import Breakpoint from "../breakpoint/breakpoint"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faSun, faMoon } from '@fortawesome/free-solid-svg-icons'
import { faMoon as regMoon } from '@fortawesome/free-regular-svg-icons'

import { Close } from '@material-ui/icons';


const ThemeToggler = (props) => {
    const { toggleTheme, isLightTheme } = useContext(ThemeContext);
    let f;
    if (isLightTheme === 1) {
        f = (<FontAwesomeIcon icon={faSun} className="sun-icon" />)
    } else if (isLightTheme === 2) {
        f = (<FontAwesomeIcon icon={regMoon} className="moon-icon" />)
    } else {
        f = (<FontAwesomeIcon icon={faMoon} className="cloud-icon" />)
    }

    let c = props.className + " nav-link "
    return (
        <div className={c} onClick={toggleTheme} style={{ width: "1rem" }}>{f}</div>
    );
}


const NavbarComponent = (props) => {

    const [Background, setBackground] = useState("light")
    const [Variant, setVariant] = useState("light")
    const [openSideBar, setOpenSideBar] = useState(0)
    let { isLightTheme } = useContext(ThemeContext)
    function handleLogout() {
        console.log("Logout")
    }

    useEffect(() => {
        if (isLightTheme === 1) {
            // setBackground("light");
            setBackground(null);
            setVariant("light");
        } else {
            setBackground("dark");
            setVariant("dark");
        }
    }, [isLightTheme])

    function collapseNav() {

        let q = document.querySelectorAll("button.navbar-toggler")

        if (q !== []) {
            q[0].click()
        }
    }

    function handleNavOpener() {
        setOpenSideBar((openSideBar + 1) % 2);
        // alert("clicked")
    }


    let nav_classes = `navbar navbar-expand-md bg-${Background} navbar-${Variant}`

    let side_nav_classes = `side-nav ${(openSideBar === 1) ? "opened" : ""}`;
    return (
        <>

            <Breakpoint xs sm>
                <div className="nav-side-container noselect">
                    <div className={side_nav_classes} >
                        <div className="contents">

                            <div className="content-block p-2">
                                <div className="float-right m-0 ml-auto p-0" onClick={handleNavOpener}>
                                    <Close />
                                </div>
                            opened
                            <div>
                                    Links go here <br />
                                please design a layout
                            </div>

                            </div>


                            {/* </span> */}


                        </div>

                        <div className="closer"
                            onClick={handleNavOpener}>
                        </div>
                    </div>
                </div>

            </Breakpoint>
            <div className="nav-custom-container container fixed-top">
                <nav className={nav_classes}>

                    <Link className="navbar-brand ml-auto mr-auto hide-xs hide-sm show-md" to="/">Lattice</Link>

                    <span className="show-xs show-sm hide-md" style={{ width: "60px" }}>
                        <span className="cs-nav-toggler collapsed" onClick={handleNavOpener}>
                            <span className="icon-bar top-bar"></span>
                            {/* <span className="icon-bar middle-bar"></span> */}
                            <span className="icon-bar bottom-bar"></span>
                        </span>
                    </span>
                    <Link className="navbar-brand text-center show-xs show-sm hide-md ml-auto mr-auto p-0" to="/">Lattice</Link>
                    <span className="show-xs show-sm hide-md" style={{ width: "60px" }}>
                        <ThemeToggler className="show-xs show-sm hide-md ml-auto p-0" />
                    </span>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">

                        <form className="form-inline search-box text-center m-auto">
                            <input className="form-control" type="search" placeholder="Search" aria-label="Search" />
                            <FontAwesomeIcon icon={faSearch} className="search-icon" />

                            {/* <div className="search-result">
                                one <br/>
                                two <br/>
                                three <br/>
                                four <br/>
                                five <br/>
                                ewdofewpf
                                deiwon
                                dekwnodewn
                            </div> */}
                        </form>

                        <ul className="navbar-nav ml-auto">


                            <ThemeToggler className="hide-xs hide-sm show-md mr-4" />

                            <NavLink onClick={collapseNav} to="/dashboard" className="nav-link">
                                Dashboard
                            </NavLink>

                            <Nav.Link href="/edit">Edit</Nav.Link>

                            <NavLink onClick={collapseNav} to="/about" className="nav-link">
                                About
                            </NavLink>

                            <NavDropdown title="Dropdown" id="collasible-nav-dropdown">

                                <NavDropdown.Item onClick={collapseNav} as={Link} to="/settings">Settings</NavDropdown.Item>
                                <NavDropdown.Item onClick={collapseNav} as={Link} to="/login">Login</NavDropdown.Item>
                                <NavDropdown.Item onClick={collapseNav} as={Link} to="/register">Register</NavDropdown.Item>

                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#logout" onClick={handleLogout}>Logout</NavDropdown.Item>
                            </NavDropdown>

                        </ul>

                    </div>
                </nav>
            </div>
        </>
    );
}

export default NavbarComponent;