import React, { useEffect } from 'react';
// import 'bootstrap/dist/css/bootstrap.min.css';
import "./themes/scss/App.scss";
import "./util.css";
// import dark from "./themes/dark.js";
// import dark_blue from "./themes/dark_blue.js";

// import  "./themes/dark_blue.css";
// import  "./themes/dark.css";

import ThemeContextProvider from "./contexts/theme_context";

import Breakpoint from "./components/breakpoint/breakpoint"

// import Navbar from "./components/navbar"
import Navbar from "./components/Toolbar/toolbar"
import BottomNavbar from "./components/Toolbar/bottomNav"

import Home from "./components/home"
import Dashboard from "./components/userDashboard"
// import Loader from "./components/loader"
import Settings from "./components/settings"
import Login from "./components/login"
import Register from "./components/register"
import Edit from "./components/editorjs"
import Read from "./components/readArticles"
import Page404 from "./components/404.js"
// import DynamicSheet from "./component/DynamicSheet";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  // Link
} from "react-router-dom";


function App() {

  useEffect(() => {
    document.getElementById("root").style.display = "block";
    document.getElementById("page-loader").style.display = "none";
    document.title = "Lattice";
  });

  return (
    <>

      <ThemeContextProvider>
        <Router>
          <div>
            <Breakpoint xs sm md lg xl>
              <Navbar />
            </Breakpoint>


            <Breakpoint xs sm>
              <div className="fixed-bottom">
                <BottomNavbar />
              </div>
            </Breakpoint>

            <br />
            <br />
            <br />
            {/* <br/> */}
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route path="/about">
                <div>About</div>
              </Route>
              <Route path="/dashboard">
                <Dashboard />
              </Route>

              <Route path="/settings">
                <Settings />
              </Route>

              <Route path="/login">
                <Login />
              </Route>

              <Route path="/register">
                <Register />
              </Route>

              <Route path="/edit">
                <Edit />
              </Route>

              <Route path="/read">
                <Read />
              </Route>

              {/* <Route path="/e2">
                <Edit2 />
              </Route> */}

              <Route path="*">
                <Page404 />
              </Route>
            </Switch>

          </div>

        </Router>

      </ThemeContextProvider>
      {/* </div> */}

    </>
  );
}

export default App;
