import React from 'react';

import { Link } from "react-router-dom";

import { Home, HomeOutlined, Search, SearchOutlined, Create, CreateOutlined, Person, PersonOutlined } from '@material-ui/icons';

const BottomNav = (props) => {

    const [activeTab, setActiveTab] = React.useState(1)

    function getIcon(type = 1) {
        if (type === 1) {
            if (activeTab === 1) {
                return (<Home className="bt-nav__icon" />)
            } else {
                return (<HomeOutlined className="bt-nav__icon" />)
            }
        } else if (type === 2) {
            if (activeTab === 2) {
                return (<Search className="bt-nav__icon" />)
            } else {
                return (<SearchOutlined className="bt-nav__icon" />)
            }
        } else if (type === 3) {
            if (activeTab === 3) {
                return (<Create className="bt-nav__icon" />)
            } else {
                return (<CreateOutlined className="bt-nav__icon" />)
            }
        } else if (type === 4) {
            if (activeTab === 4) {
                return (<Person className="bt-nav__icon" />)
            } else {
                return (<PersonOutlined className="bt-nav__icon" />)
            }
        }
    }

    function handleClick(n) {
        setActiveTab(n)
    }

    return (
        <>
            <div className="bt-nav">
                <div className="bt-tabs">
                    <ul className="bt-nav__ul">
                        <li className="bt-nav__item" onClick={() => { handleClick(1) }}>
                            <Link to="/" className="bt-nav__link">
                                {getIcon(1)}
                            </Link>
                        </li>
                        {/* <li className="bt-nav__item" onClick={() => { handleClick(2) }}>
                            <a href="#search" className="bt-nav__link">
                                {getIcon(2)}
                            </a>
                        </li> */}
                        <li className="bt-nav__item" onClick={() => { handleClick(3) }}>
                            <Link to="/edit" className="bt-nav__link">
                                {getIcon(3)}
                            </Link>
                        </li>
                        <li className="bt-nav__item" onClick={() => { handleClick(4) }}>
                            <Link to="/settings" className="bt-nav__link">
                                {getIcon(4)}
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </>
    );
}

export default BottomNav;